import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useForm, Controller } from "react-hook-form";
import Grid from '@material-ui/core/Grid';
import Input from "@material-ui/core/Input";
import Button from '@material-ui/core/Button';
import { db } from '../../../utils/firebase';



const useStyles = makeStyles((theme) => ({
  form: {
    width: 1000,
    margin: theme.spacing(0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
}));

export default function AddPhrase({addPhrase, count}) {
  const classes = useStyles();
  const { handleSubmit, formState: { errors }, control, reset } = useForm();

  const onSubmit = async data => {
    console.log(data, addPhrase);
    await db.collection("phrases").add({
      ...data,
      createdAt: new Date().toISOString()
    })
    reset({ meaning: '' });
    addPhrase(count + 1);
    // db.collection("phrases").orderBy("createdAt", "desc").limit(1000).get()
    //   .then(querySnapshot => querySnapshot.docs.map(item => item.data())) // .then(console.log);
    //   .then(phrases => setPhrases(phrases));
  };
  
  return (
    <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
      <Grid
        container
        direction='column'
        justify='center'
        alignItems='center'
        spacing={1}
      >
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name='title'
            fullWidth
            control={control}
            rules={{ required: true }}
            defaultValue=''
            placeholder='Phrase'
            render={({ field }) => <Input {...field} fullWidth />}
          />
          {errors.title && <span>Title is required</span>}
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Controller
            name='meaning'
            control={control}
            defaultValue=''
            render={({ field }) => <Input {...field} fullWidth />}
          />
        </Grid>
        <Grid container item xs={12} spacing={1} className={classes.input}>
          <Button type='submit' variant='contained' color='primary'>
            {' '}
            Submit{' '}
          </Button>
        </Grid>
      </Grid>
    </form>
  );
}
