import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import List from '@material-ui/core/List';
import { makeStyles } from '@material-ui/core/styles';
import PhraseItem from '../PhraseItem'
import { db } from '../../../utils/firebase';
import { selectPhrases, setPhrases } from '../phraseSlice';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function PhraseList() {
  const phrases = useSelector(selectPhrases);
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    db.collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      ) // .then(console.log);
      .then((phrases) => dispatch(setPhrases(phrases)));
  }, []);

  return (
    <List component='nav' className={classes.root} aria-label='secondary mailbox folders'>
      {phrases.map((phrase) => (
        <PhraseItem phrase= {phrase} key={phrase.id}/>
      ))}
    </List>
  );
}
